@import url("../../../assets/feather/style.min.css");
@import url("../../../assets/fontawesome/css/all.css");
@import url("../../../assets/line-awesome/css/line-awesome.min.css");
:host {
  display: block;
}

.tobeAssignedHeader {
  font-weight: 500;
  letter-spacing: 0.05rem;
  font-size: 1.12rem;
  padding-top: 5px;
  margin-bottom: 1rem;
}

.closeBtn {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  font-size: 1.75em;
  line-height: 1em;
  padding: 0.4rem;
}

.closeBtn:hover {
  background-color: #f6f6f6;
}

.stickyHeader {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #ffffff;
  z-index: 1;
}

.pointer {
  cursor: pointer;
}
